




















import { api } from '@/services/ApiService';
import {
    Component,
    Vue
} from "vue-property-decorator";

@Component({
    metaInfo() {
        return {
            title: "Déconnexion"
        }
    }
})
export default class Logout extends Vue {
    mounted() {
        let redirectDelay = 0;
        if (api.loggedIn()) {
            api.clearToken();
            redirectDelay = 250;
        }
        setTimeout(() => this.$router.push({ name: 'Home' }).catch(e => console.log(e)), redirectDelay)
    }

}
