









































































































































































































































import {
    Component,
    Vue
} from "vue-property-decorator";
import Alert from '@/components/Alert.vue';
import Spinner from '@/components/Spinner.vue';
import {
    Activity,
    ActivityType
} from '@/services/ApiService';

import {
    api,
    Person
} from '@/services/ApiService';
@Component({
    components: {
        Alert,
        Spinner
    },
    metaInfo() {
        return {
            title: "CV"
        }
    }
})
export default class CV extends Vue {
    error = "";
    returnText = "Retourner à la liste des personnes.";
    returnUrl = "/persons";
    person: Person | null = null;
    loading = false;
    activityErrors: any = null;

    self = false;
    isActivity = false;

    toDelete: Activity | null = null;
    toEdit: Activity | null = null;
    editMode = false;

    refresh(): void {
        let id = -1;
        try {
            id = parseInt(this.$route.params.id);
            this.loading = true;
            api.person(id).subscribe(r => {
                if (!r) {
                    this.error = "La personne à l'identifiant '" + id + "' n'existe plus."
                } else {
                    this.person = r;
                    document.title = `CVthèque - ${this.$options.filters?.capitalize(this.person.lastname)} ${this.$options.filters?.capitalize(this.person.firstname)}`
                }
                if (api.loggedIn()) {
                    api.account().subscribe(a => {
                        if (a && a.id == r.id) this.self = true;
                        this.loading = false;
                    });
                } else {
                    this.loading = false;
                }
            });
        } catch (e) {
            console.log(e);
        }
    }

    activityBadge(activity: Activity): string | undefined {
        return activity.type?.substring(0, 2).toUpperCase();
    }

    deleteActivity(activity: Activity, confirm: boolean | null): void {
        if  (!this.person?.activities) return;

        if (!confirm) {
            this.toDelete = activity;
            document.getElementById("deleteBtn")?.click();
        } else {
            api.deleteActivity(activity.id).subscribe(r => {
                this.person = r;
            });
        }
    }

    editActivity(activity: Activity, finished: boolean): void {
        this.activityErrors = null;
        if  (!this.person?.activities) return;
        if (!finished) {
            this.editMode = true;
            this.toEdit = { ... activity }; // clone
            document.getElementById("inputBtn")?.click();
        } else {
            api.editActivity(activity).subscribe(r => {
                this.person = r;
                document.getElementById("closeInputModal")?.click();
            }, e => {
                if (e.response?.status == 400 && e.response?.data) {
                    this.activityErrors = e.response.data;
                }
            });
        }
    }


    addActivity(activity: Activity): void {
        this.activityErrors = null;
        if (!this.person?.activities) return;
        if (!activity) {
            this.editMode = false;
            this.toEdit = {
                id: 0,
                year: (new Date()).getFullYear(),
                type: ActivityType.PROFESSIONAL
            }
            document.getElementById("inputBtn")?.click();
        } else {
            api.addActivity(activity).subscribe(r => {
                this.person = r;
                document.getElementById("closeInputModal")?.click();
            }, e => {
                if (e.response?.status == 400 && e.response?.data) {
                    this.activityErrors = e.response.data;
                }
            });
        }
    }

    mounted(): void {
        this.refresh();
    }
}
