



























































































import Alert from '@/components/Alert.vue';
import Spinner from '@/components/Spinner.vue';
import {
    api,
    UserEdit,
    User
} from '@/services/ApiService';
import {
    Component,
    Vue 
} from "vue-property-decorator";

@Component({
    components: {
        Alert,
        Spinner
    },
    metaInfo() {
        return {
            title: "Compte"
        }
    }
})
export default class Account extends Vue {
    alertMessage = "";
    alertSuccess = false;
    loading = false;
    account: User | null = null;
    form: UserEdit | null = null;
    errors: any = null;

    mounted() {
        api.account().subscribe(r => {
            this.account = r;
            this.map();
        });
    }

    map(): void {
        if (this.account) {
            this.form = {
                id: this.account.id,
                firstname: this.account.firstname,
                lastname: this.account.lastname,
                birthdate: this.account.birthdate,
                website: this.account.website
            }
        }
    }

    edit(): void {
        if (this.form) {
            this.loading = true;
            this.alertMessage = "";
            this.errors = null;
            api.edit(this.form).subscribe(r => {
                this.account = r;
                this.map();
                this.alertSuccess = true;
                this.alertMessage = "Modification apportées.";
                this.loading = false;
            }, e => {
                if (e.response?.status == 400 && e.response?.data) {
                    this.errors = e.response.data;
                    this.alertSuccess = false;
                    this.alertMessage = "Modifications non apportées, veuillez corriger les erreurs ci-dessous."
                }

                this.loading = false;
            });
        }
    }
}
