
















































import Alert from '@/components/Alert.vue';
import Spinner from '@/components/Spinner.vue';
import { api } from '@/services/ApiService';

import {
    Component,
    Vue,
    Provide
} from "vue-property-decorator";

@Component({
    components: {
        Alert,
        Spinner
    },
    metaInfo() {
        return {
            title: "Connexion"
        }
    }
})
export default class Login extends Vue {

    @Provide() email = ""
    @Provide() password = ""
    loading = false
    error = ""
    errors: any = null;

    login(e: Event): void {
        this.error = ""
        this.loading = true;
        this.errors = null;

        api.signin(this.email, this.password).subscribe(r => {
            api.setToken(r.data);
            this.$router.push({ name: "Account" }).catch(e => console.log(e));

        }, e => {
            if (e.response?.status == 400 && e.response?.data) {
                this.errors = e.response.data;
                this.error = "Formulaire de connexion invalide, veuillez corriger les erreurs ci-dessous."
                this.errors = e.response.data;
            }
            else if (e.response.status == 500) this.error = "Informations de connexion invalides.";

            this.loading = false
        });
        
        e.preventDefault();
    }
}
