



































































import Alert from '@/components/Alert.vue';
import Spinner from '@/components/Spinner.vue';
import {
    api,
    UserInvite
} from '@/services/ApiService';
import {
    Component,
    Vue 
} from "vue-property-decorator";

@Component({
    components: {
        Alert,
        Spinner
    },
    metaInfo() {
        return {
            title: "Cooptation"
        }
    }
})
export default class Invite extends Vue {
    alertMessage = "";
    loading = false;
    alertSuccess = false;
    errors: any = null;

    empty: UserInvite =  {
        password: Math.random().toString(36).slice(-8)
    }

    form: UserInvite = { ... this.empty } // clone

    invite() {
        this.alertMessage = "";
        this.loading = true;
        this.errors = null;
        api.invite(this.form).subscribe(r => {
            this.form = { ... this.empty } // clone
            this.alertSuccess = true;
            this.alertMessage = "Personne ajoutée avec succès.";
            this.loading = false;
        }, e => {
            if (e.response?.status == 400 && e.response?.data) {
                this.errors = e.response.data;
                this.alertSuccess = false;
                this.alertMessage = "Formulaire invalide, veuillez corriger les erreurs ci-dessous.";
            }

            this.loading = false;
        });
    }
}
